import React, { useState, useEffect } from 'react';
import Structure from '../Structure';
import mixpanel from '@components/mixpanel';
import styled from 'styled-components';

const Container = styled.div`
    flex: 1;
    height: calc(100vh - 80px);
    width: 100%;
    padding-top: 50px;
`;

const PageTitle = styled.div`
    width: 100%;
    text-align: center;
    font-size: 18px;
    font-weight: 900;
`;

export default function Policy() {
    useEffect(() => {
        //mixpanel
        mixpanel.track('Policy Page', {
            'Opted out of email': true,
            'ip': '0',
        });
    }, []);

    return (
        <>
            <Structure fullpage>
                <Container className="container">
                    <PageTitle>SOJIAARE PRIVACY POLICY</PageTitle>
                    <p>Effective date: 12 April 2022</p>
                    <p>
                        Sojiare is firmly committed to the security and protection of our Users' personal information
                        and contacts. This Privacy Policy describes how Sojiare will collect, use, share and process
                        personal information. Capitalized terms not defined in this Privacy Policy are defined in the
                        Sojiare Terms of Service.
                    </p>
                    <p>
                        By accepting the Sojiare Privacy Policy and/or using the Services, You consent to collecting,
                        sharing, and processing personal information as described herein. Suppose You provide us with
                        personal information about someone else. In that case, You confirm that they are aware that You
                        have provided their information and that they consent to our use of their information according
                        to our Privacy Policy. You may opt out at any time to prevent further use of the information
                        shared via the Services.
                    </p>

                    <b>PERSONAL INFORMATION COLLECTED</b>
                    <b>1.1 USER PROFILE</b>
                    <p>
                        When You create a user profile in the Services and confirm being the holder of a certain number,
                        Sojiare will collect the information provided by You. To create a user profile, You must
                        register Your first name, last name and phone number. Additional information that may be
                        provided at Your option includes, but is not limited to, gender, street address and zip code,
                        country of residence, email address, and a short status message. Sojiare may supplement your
                        information with information from third parties and add it to the information provided by You,
                        e.g. demographic information and additional publicly available contact information.
                    </p>

                    <b>1.2 INSTALLATION AND USE</b>
                    <p>
                        When You install and use the Services, Sojiare will collect personal information from You and
                        any devices You may use to interact with our Services. This information may include e.g.:
                        geo-location; Your IP address; device ID or unique identifier; device manufacturer and type;
                        device and hardware settings; SIM card usage; applications installed on your device; ID for
                        advertising; ad data, operating system; web browser; operator; IMSI; connection information;
                        screen resolution; usage statistics; default communication applications; access to device
                        address book; device log and event information; logs, keywords and meta data of incoming and
                        outgoing calls and messages; version of the Services You use and other information based on Your
                        interaction with our Services such as how the Services are being accessed (via another service,
                        web site or a search engine); the pages You visit and features you use on the Services; the
                        services and websites You engage with from the Services; content viewed by You, content You have
                        commented on or sent to us and information about the ads You see and/or engage with; the search
                        terms You use; order information and other usage activity and data logged by Sojiare's servers
                        from time to time. Sojiare may collect some of this information automatically through cookies,
                        and You can learn more about our use of cookies in our Cookie Policy.
                    </p>

                    <b>1.3 THIRD-PARTY SERVICES</b>
                    <p>
                        You may provide us with Your user identifier information regarding, or enable Your usage of,
                        certain third-party services (together with a mapped photo where applicable), e.g. social
                        networking services and payment services, in order for the Services to interoperate with such
                        third party services. You may use such third party services to create Your user profile or log
                        in to our Services, share information with such third party services, or connect Your user
                        profile with the respective third party services. Such third party services may automatically
                        provide us with access to the specific personal information retained by them about You (e.g.,
                        your payment handle, unique identification information, content viewed by You, content liked by
                        You and information about the advertisements You have been shown or may have clicked on) and You
                        agree that we may collect, use and retain the information provided by these third party services
                        in accordance with this Privacy Policy. You may be able to control the personal information You
                        allow us to have access to through the privacy settings on the respective third party service.
                        We will never store any passwords created for any third party services.
                    </p>

                    <b>1.4 CONTACT INFORMATION</b>
                    <p>
                        Where the Sojiare mobile applications ("Sojiare Apps") are obtained from other sources than
                        Apple App Store or Google Play, You may share the names, numbers, Google ID's and email
                        addresses contained in Your address book ("Contact Information") with Sojiare by enabling the
                        Sojiare Enhanced Search Functionality. Where the Sojiare Apps are obtained from Apple App Store
                        or Google Play, we do not share any user Contact Information. In addition to Contact
                        Information, if You choose to activate the use of third-party services, such as social networks
                        services, within the Services, Sojiare may collect, store and use the list of identifiers
                        associated with said services linked to the Contact Information in order to enhance the results
                        shared with other Users. Please note that no other contact information other than the phone
                        numbers and attached names, Google IDs, and email addresses will be collected and used from Your
                        address book. Other numbers or information that may be contained in Your address book will be
                        filtered away by our safety algorithms and, therefore, not be collected by Sojiare. Please also
                        note that You can always choose not to share Contact Information with Sojiare and if You have
                        shared such information and changed Your mind, You can delist Your number or opt-out to render
                        Your entire Contact Information unavailable for search in the Sojiare database.
                    </p>

                    <b>1.5 OTHER INFORMATION YOU MAY PROVIDE</b>

                    <p>
                        You may provide us with various information about your interaction with our Services. For
                        example, You may associate a phone number not registered in the Sojiare database or belongs to a
                        User with a business or name through tagging functionality. You may report a phone number as
                        spam or other attributes in spam blocking directories.
                    </p>

                    <p>
                        Sojiare may also, from time to time, offer You the opportunity to provide information on Your
                        experience of using the Services, surveys or other promotions. Sojiare will collect the
                        information You provide in connection therewith, as well as any other information You provide
                        through the Services or when You communicate or interact with us.
                    </p>

                    <b>2. USE OF PERSONAL INFORMATION</b>
                    <b>2.1 PROVIDE, IMPROVE AND PERSONALIZE OUR SERVICES</b>

                    <p>
                        Sojiare may use the personal information collected to provide, maintain, improve, analyze and
                        personalize the Services to its Users, partners and third-party providers. More specifically,
                        Sojiare may use such information to: Provide smart caller ID, dialer and messaging functionality
                        that, among other features, allow for population of unidentified numbers and other data in call
                        or message logs, display the name associated with a certain number for incoming or outgoing
                        calls and messages or following a manual number search against the Sojiare database. Display the
                        number associated with a specific name following a manual name search against the Sojiare
                        database; subject to that; name searches will only produce a number linked to the name if the
                        number is available for search in a public database or relates to a User who has made their name
                        available for search. Send in application push notifications and reminders and deliver messages
                        via the Services, in which case Your messages will be kept for a limited period of time in order
                        to deliver the messages. Maintain User spam lists and build a community-based spam blocking
                        directory; Enable You to use and share Your information in connection with Your registration,
                        login or other use of third-party services, e.g. payment service providers, online services,
                        social networking sites and other third-party APIs; and Otherwise improve our Services, business
                        and operations.
                    </p>

                    <b>2.2 STATISTICAL DATA FROM THE SERVICES</b>

                    <p>
                        We use aggregated or anonymized personal information for statistical and analytical purposes. We
                        may come to share such data with third parties. We do not consider personal information to
                        include information that has been made anonymous or aggregated so that it can no longer be used
                        to identify a specific person, whether in combination with other information or otherwise.
                    </p>

                    <b>2.3 PERSONALIZE OUR ADVERTISING AND COMMUNICATIONS</b>

                    <p>
                        We may use any of the information collected, as set out above, to provide You with location and
                        interest-based advertising, marketing messaging, information and services. We may also use the
                        collected information to measure the performance of our advertising and marketing services. We
                        may contact You for verification purposes or information on the Services or special offers, e.g.
                        newsletter emails, SMS and similar notifications about Sojiare's and our business partners'
                        products and services. We also use the collected information to respond to you when you contact
                        us.
                    </p>

                    <b>2.4 COMPLIANCE WITH LAWS AND PREVENTION OF FRAUDULENT OR ILLEGAL ACTIVITIES</b>
                    <p>
                        We may use the collected information to comply with applicable laws to enforce our agreements,
                        protect and defend the rights or safety of Sojiare, its Users or any other person and verify
                        provided User profile information with third party providers and ensure technical service
                        functionality and data accuracy, perform trouble-shooting and prevent or detect fraud, security
                        breaches or illegal activities.
                    </p>

                    <b>3. SHARING AND DISCLOSURE OF PERSONAL INFORMATION</b>

                    <p>
                        In addition to the sharing and disclosure of personal information that is included as part of
                        the functionality of the Services as described in section 2 above, Sojiare may disclose personal
                        information if we believe such action is necessary to: Comply with the law or legal process
                        served on us;
                    </p>

                    <p>
                        Protect and defend our rights and the enforcement of our agreements; or Protect the security and
                        safety of Users or members of the public or other aspects of public importance, provided that
                        such disclosure is lawful.
                    </p>

                    <p>
                        Sojiare will always require these third parties to take appropriate organizational and technical
                        measures to protect personal information and observe applicable legislation. Sojiare may also
                        share personal information with third-party advertisers, agencies and networks. Such third
                        parties may use this information for analytical and marketing purposes, e.g. to provide
                        measurement services and targeted ads and improve products and services. Such third parties may
                        collect the information by using cookies or similar technologies, and You can learn more about
                        the use of cookies in our Cookie Policy. We may disclose and transfer Your Information to our
                        associated or affiliated organizations or related entities and to any third party who acquires
                        our business, whether such acquisition is by merger, consolidation or purchase of all or a
                        substantial portion of our assets.
                    </p>

                    <b>4. DELISTING OF CONTACT INFORMATION AND OPT-OUT OF AD TARGETING</b>

                    <p>
                        Suppose a User chooses to disable the Enhanced Search Functionality. In that case, the Contact
                        Information made available by that User is disabled and will not be available for search in the
                        Sojiare database. Suppose any persons do not wish to have their names and phone numbers made
                        available through the Enhanced Search or Name Search functionalities. They can exclude
                        themselves from further queries by notifying Sojiare via its website at www.sojiare.com or as
                        outlined in the contact details below. You acknowledge and agree that Sojiare may keep and
                        process personal information related to such requests in order to honour the request. You can
                        limit or opt out of the collection and use of Your information for ad targeting by third parties
                        via Your device settings.
                    </p>

                    <b>5. ACCESSING AND UPDATING PERSONAL INFORMATION</b>

                    <p>
                        Sojiare may on its own initiative, or at Your request, replenish, rectify or erase any
                        incomplete, inaccurate or outdated personal information retained by Sojiare in connection with
                        the operation of the Services. When required by applicable law, You have the right to know what
                        personal information is stored about You and to have any such information corrected or deleted
                        upon Your request. See contact details below.
                    </p>

                    <b>6. MINORS</b>

                    <p>
                        Sojiare does not intentionally or knowingly collect personal information through the Services
                        from anyone under the relevant age of consent under the laws of their respective jurisdictions.
                        We encourage parents and guardians to be involved in their children's online activities to
                        ensure that no personal information is collected from a child without their consent.
                    </p>

                    <b>7. CHANGES TO THIS PRIVACY POLICY</b>
                    <p>
                        Sojiare may, at any time, with or without a separate notice, change this Privacy Policy, and You
                        are encouraged to review this Policy from time to time. In case of substantial changes, Sojiare
                        will notify the Users by push notice or via notice in the Services. Your continued use of the
                        Services after a notice of changes has been communicated to You or published on our Services
                        shall constitute consent to the changed Policy.
                    </p>

                    <b>8. COOKIES</b>

                    <p>
                        We use cookies, web beacons, flash cookies, HTML 5 cookies, pixel tags and other web application
                        software methods, including mobile application identifiers, to help us recognize you across the
                        Services, learn about your interests on the Services, improve your experience, increase
                        security, and measure use and effectiveness of the Services. You can control cookies through
                        your browser settings and other tools. By interacting with the Services, you consent to the use
                        and placement of cookies on your device per this Privacy Policy, which incorporates our Cookie
                        Policy.
                    </p>

                    <b>9. CONTACT</b>
                    <p>
                        If You have any additional questions about Sojiare's Privacy Policy or want to request certain
                        personal information, You are encouraged to contact Sojiare. The contact information is:
                        "hi@sojiare.com, 190 Holiday Street, Birmingham, B1 1FD. 07587530253"
                    </p>

                    <p>
                        You can also send your enquiries (concerning any grievances to our Grievances Officer) via email
                        to www.sojiare.com.
                    </p>
                </Container>
            </Structure>
        </>
    );
}
